import MarkdownIt from 'markdown-it';
import hljs from "highlight.js/lib/common";

export default {
    install: (app: any, options: any) => {
        const markdown = new MarkdownIt({
            html: true,
            linkify: true,
            typographer: true,
            highlight: function (str: string, lang: string) {
                if (lang) {
                    return hljs.highlight(str, { language: lang }).value;
                }
                return ""
                // return hljs.highlight(str,{language:lang}).value;
            }
        })
        markdown.renderer.rules.image = function (tokens: any, idx: any, options: any, env: any, slf: any) {
            const token = tokens[idx]
            token.attrs[token.attrIndex('alt')][1] = slf.renderInlineAsText(token.children, options, env)
            token.attrSet('loading', 'lazy')
            return slf.renderToken(tokens, idx, options)
        }
        markdown.use(require("markdown-it-anchor").default);
        markdown.use(require("markdown-it-table-of-contents"));

        app.config.globalProperties.$markdown = markdown
        app.provide("markdown", markdown);
    }
};