import { createWebHistory, createRouter } from "vue-router";
import { app } from '@/main';

const routes = [
  {
    path: "/",
    name: "Profile",
    component: () => import('./views/Profile.vue'),
    meta: {
      transition: {
        BlogHome: "slide-left",
      },
      head: {
        title: `Alejandro Suárez`,
        meta: [
          {
            name: `description`,
            content: `Ingeniero informático con residencia en Galicia.`,
          },
        ],
      },
    },
  },
  {
    path: "/videochat",
    name: "VideoChat",
    component: () => import('./views/VideoChat.vue'),
    meta: {
     
      head: {
        title: `Videochat`,
        meta: [
          {
            name: `description`,
            content: `P2P videochat.`,
          },
        ],
      },
    },
  },

  {
    path: "/blog",
    name: "Blog",
    component: () => import('./views/Blog.vue'),
    children: [
      // UserHome will be rendered inside User's <router-view>
      // when /user/:id is matched
      {
        name: "BlogHome",
        path: "",
        component: () => import('./views/Blog/Home.vue'),
        meta: {
          transition: {
            Profile: "slide-right",
          },
        },
      },
      {
        name: "Post",
        path: ":id",
        props: true,
        component: () => import('./views/Blog/Post.vue'),
      },
      // ...other sub routes
    ],
    meta: {
      transition: {
        Profile: "slide-right",
      },
      head: {
        title: `Alejandro Suárez - Blog`,
        meta: [
          {
            name: `description`,
            content: `Blog de Alejandro Suárez.`,
          },
        ],
      },
    },
  },
  // { path: '/:pathMatch(.*)*', name:"Not found", component: () => import('./views/PageNotFound.vue') },

];

const router = createRouter({
  history: createWebHistory(),
  routes,
  scrollBehavior(to, from, savedPosition) {


    if ((from.name == undefined) || (to.path == "/" && from.path.includes("/blog")) || (to.path.includes("/blog") && from.path == "/")) {
      app.config.globalProperties.$bus.off_all("triggerScroll")
      if (savedPosition) return savedPosition
      return
    }

    return new Promise((resolve, reject) => {

      console.log(savedPosition)
      // console.log("scrollBehavior", to, from, savedPosition);
      if (savedPosition)
        return resolve(savedPosition)

      app.config.globalProperties.$bus.on("triggerScroll", function triggerScroll() {

        app.config.globalProperties.$bus.off_all("triggerScroll")

        if (savedPosition)
          return resolve(savedPosition)
        return resolve({ top: 0, left: 0 })
      })
    });
  }
});

router.beforeEach((to, from) => {
  router.prevRoute = from;
});

export default router;
